<template>
  <div class="language-switcher">
    <el-dropdown @command="handleLanguageChange" trigger="click">
      <div class="language-trigger">
        <span class="language-flag">{{ currentLanguageFlag }}</span>
        <span class="language-name">{{ currentLanguageName }}</span>
        <el-icon class="dropdown-icon">
          <ArrowDown />
        </el-icon>
      </div>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item
            v-for="lang in languageOptions"
            :key="lang.code"
            :command="lang.code"
            :class="{ 'is-active': lang.code === currentLang }"
          >
            <span class="language-flag">{{ lang.flag }}</span>
            <span class="language-name">{{ lang.name }}</span>
          </el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </div>
</template>

<script setup>
import { computed } from 'vue'
import { useRouter } from 'vue-router'
import { useLanguageStore } from '@/store/language'
import { ArrowDown } from '@element-plus/icons-vue'

const router = useRouter()
const languageStore = useLanguageStore()

const currentLang = computed(() => languageStore.currentLang)
const languageOptions = computed(() => languageStore.languageOptions)

const currentLanguageFlag = computed(() => {
  const lang = languageOptions.value.find(l => l.code === currentLang.value)
  return lang ? lang.flag : 'ğŸ‡¨ğŸ‡³'
})

const currentLanguageName = computed(() => {
  const lang = languageOptions.value.find(l => l.code === currentLang.value)
  return lang ? lang.name : 'ä¸­æ–‡'
})

const handleLanguageChange = (langCode) => {
  if (langCode === currentLang.value) return
  
  // è·å–å½“å‰è·¯å¾„ï¼ˆä¸åŒ…å«è¯­è¨€å‰ç¼€ï¼‰
  const currentPath = languageStore.getCurrentPathWithoutLang()
  
  // è®¾ç½®æ–°è¯­è¨€
  languageStore.setLanguage(langCode)
  
  // æ„å»ºæ–°è·¯å¾„
  const newPath = languageStore.getLocalizedPath(currentPath)
  
  // è·³è½¬åˆ°æ–°è·¯å¾„
  router.push(newPath)
}
</script>

<style scoped>
.language-switcher {
  display: inline-block;
}

.language-trigger {
  display: flex;
  align-items: center;
  padding: 8px 12px;
  color: #fff;
  cursor: pointer;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  font-size: 14px;
}

.language-trigger:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.language-flag {
  margin-right: 6px;
  font-size: 16px;
}

.language-name {
  margin-right: 4px;
  font-weight: 500;
}

.dropdown-icon {
  font-size: 12px;
  transition: transform 0.3s ease;
}

.language-switcher:hover .dropdown-icon {
  transform: rotate(180deg);
}

/* ä¸‹æ‹‰èœå•æ ·å¼ */
:deep(.el-dropdown-menu__item) {
  display: flex;
  align-items: center;
  padding: 8px 16px;
}

:deep(.el-dropdown-menu__item.is-active) {
  background-color: #f0f9ff;
  color: #409eff;
}

:deep(.el-dropdown-menu__item:hover) {
  background-color: #f5f7fa;
}

/* ç§»åŠ¨ç«¯é€‚é… */
@media (max-width: 768px) {
  .language-trigger {
    padding: 6px 8px;
    font-size: 13px;
  }
  
  .language-name {
    display: none;
  }
  
  .language-flag {
    margin-right: 0;
  }
}
</style>
